import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login.vue'
import Start from '@/components/start.vue'
import FlowEpg from '@/components/flow_epg.vue'
import FlowVmm from '@/components/flow_vmm.vue'
import GetSubnets from '@/components/get_subnets.vue'
import FlowContract from '@/components/flow_contract.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
    routes: [
      {
        path: '/',
        name: 'Login',
        component: Login
      }, 
      {
        path: '/start',
        name: 'Start',
        component: Start
      },       
      {
        path: '/flow/epg',
        name: 'FlowEpg',
        component: FlowEpg
      },
      {
        path: '/flow/vmm',
        name: 'FlowVmm',
        component: FlowVmm
      },  
      {
        path: '/get/subnets',
        name: 'GetSubnets',
        component: GetSubnets
      },      
      {
        path: '/flow/contract',
        name: 'FlowContract',
        component: FlowContract
      },
  ], 
})
