<template>
<el-container class="container">
    <el-main>
        <span class="card-title">Domain Flow</span>
        <p><el-tag v-if="domain_selection.dn" type="info"> {{domain_selection.type}} domain: {{domain_selection.name}} </el-tag><el-tag v-if="epg_selection.epg" type="info"> EPG: {{epg_selection.epg}} </el-tag> </p>
        <div v-if="succes_dm == false">
            <div v-if="route" class="flowEpg">
            </div>
            <div v-else class="flowEpg">
                <p> 1. Select an EPG you want to attach to a VMM/Physical domain </p>   
                <el-select filterable clearable v-model="epg_selection" value-key="epg" placeholder="Select a EPG">
                    <el-option v-for="epg in allepg" :label="String(epg.epg + ' | Tn: ' + epg.tn + ' | AP: ' + epg.ap)" :key="epg.id" :value="epg"></el-option>
                </el-select>                      
                <div class="domain_warning" v-if="epg_selection_domain.length > 0">
                    <p v-if="epg_selection_domain.length == 1"> Be aware this EPG already has a Domain associated: </p>
                    <p v-else> Be aware this EPG already has {{epg_selection_domain.length}} Domains associated: </p>
                    <ul v-for="epgDm in epg_selection_domain">
                        <li> Domain: <strong>{{epgDm.domain}}</strong> with policy: <strong>{{epgDm.pushpolicy}} </strong></li>   
                    </ul>
                </div>
            </div>
            <div v-if="epg_selection" class="flowEpg">
                <div>
                    <p> 2. Select a Domain where you want to add your EPG to </p>
                    <el-select filterable clearable v-model="domain_selection" value-key="dn" placeholder="Select a Domain">
                        <el-option v-for="dom in domains" :key="dom.dn" :value="dom" :label="String(dom.name + ' | ' + dom.type)"></el-option>
                    </el-select>
                    <el-radio v-model="vmm_push_policy" label="immediate">Immediate</el-radio>
                    <el-radio v-model="vmm_push_policy" label="lazy">On Demand</el-radio>
                    <div>
                        <el-button :disabled=isDomainDisabled type="primary" v-on:click.prevent="postDomain()"> Add EPG to Domain </el-button>
                    </div>

                </div>                          
            </div>
        </div>
        <div v-else class="flowEpg">
            <p> Great, EPG with name <strong>'{{ epg_selection.epg }}'</strong> has been added to domain <strong>'{{domain_selection.name }}'</strong> with policy <strong>'{{ vmm_push_policy }}'</strong>.</p>
            <p><el-button type="primary" v-on:click.prevent="createNewVmmFlow()"> Add new EPG to Domain </el-button></p>

        </div>            
    </el-main>
</el-container>
</template>


<script>

import { getVmmDom, getPhysDom, getEpgDomainCheck, getTentants, getAps, getEpgs} from '../utils/getApic';
import { parseVmmDom, parsePhysDom, parseEpgDomainCheck, parseTentants, parseAps } from '../utils/parse';
import { postEpgToDomain } from '../utils/postApic';
import moment from 'moment'
export default {
    components: {
    },
    data(){
        return {
            allepg: [],
            domains: [],
            loading: {
                domain: true,
                epg: true,
            },
            domain_selection: '',
            tenantlist: [],
            ap_name_list: [],
            epg_selection: '',
            epg_selection_domain: [],
            route: false,
            vmm_push_policy: 'immediate',
            succes_dm: false,
        }
    },

    created() {
        this.getDomains()
        if (this.$route.params.tn){
            this.route = true
            this.epg_selection = this.$route.params;
        }
        else {
            this.getEgpDomain()
        }
    },

    watch: {
        'epg_selection': function (val) {
            this.getSelectedEgpDomain()
        }
    },

    methods: {
        getDomains(){
            getVmmDom().then(response => {
                this.domains = this.domains.concat(parseVmmDom(response.data))
            })

            getPhysDom().then(response => {
                this.domains = this.domains.concat(parsePhysDom(response.data))
            })                      
            
            this.loading.tenant = false
        },

        createNewVmmFlow(){
            this.epg_selection = ''
            this.domain_selection = ''
            this.succes_dm = false
        },        

        getSelectedEgpDomain(){
            this.epg_selection_domain = []
            getEpgDomainCheck(this.epg_selection).then(response => {
                this.epg_selection_domain = this.epg_selection_domain.concat(parseEpgDomainCheck(response.data))
            })
        },

        postDomain(){
            postEpgToDomain(this.epg_selection, this.domain_selection, this.vmm_push_policy).then(response => {
                    this.succes_dm = true
                },  response => {
                   // error callback
            });
        },     

        getEgpDomain(){
            var tenantlist = []
            var aplist = []
            var ap_spec = {}
            var total = {}
            var totalegp = []

            getTentants().then(response => {
                tenantlist = parseTentants(response.data)
            })

            setTimeout( () => {
                for (let q = 0; q < tenantlist.length; q++){
                    getAps(tenantlist[q]).then(response => {
                        for (var s = 0; s < response.data.imdata.length; s++){
                            var ap_spec = {}
                            ap_spec.name = response.data.imdata[s].fvAp.attributes.name
                            ap_spec.tenant = tenantlist[q]
                            aplist.push(ap_spec)
                        }
                    })
                }

                setTimeout( () => {
                    for (let d = 0; d < aplist.length; d++){
                        getEpgs(aplist[d].tenant, aplist[d].name).then(response => {
                            for (var p = 0; p < response.data.imdata.length; p++){
                                var total = {}
                                total.ap = aplist[d].name
                                total.tn = aplist[d].tenant
                                total.epg = response.data.imdata[p].fvAEPg.attributes.name
                                totalegp.push(total)
                            }                 
                            },  response => {
                           // error callback
                        });
                    }
                    this.allepg = totalegp
                }, 1000);
            }, 1000);
            
        },            
    },

    computed: {
        isDomainDisabled(){
            return (this.epg_selection.length <= 0 || this.domain_selection.length <= 0 )
        },      
    }    
}

</script>

