import Vue from 'vue';
import axios from 'axios'
import moment from 'moment'
import { tokenValidCheck } from '../utils/auth';

export function createHeader(){
	tokenValidCheck()
	var headers = {
		'Content-Type': 'Application/json',
		'Accept': 'text/html,application/xhtml+xml,application/xml,application/json',
		'DevCookie': sessionStorage.getItem('devcookie'),
      	'APIC-challenge': sessionStorage.getItem('challenge')
	}
	return headers
}
