import Vue from 'vue';
import axios from 'axios'
import moment from 'moment'

export function tokenEndTime(){
	var now = moment().format()
	var token_endtime = moment(now).add(600, 'seconds').format()
    return token_endtime
}

export function tokenValidCheck(){
	if (sessionStorage.getItem("tokenEndTime") <= moment().format()) {
		login(sessionStorage.getItem("ip"), sessionStorage.getItem("name"), sessionStorage.getItem("pwd"))
	}
}

export function login(ip, username, pwd) {
	
	var payload = {
	    "aaaUser" : {
	        "attributes" : {
	            "name": username,
	            "pwd": pwd
	        }
	    }
	}

	axios.post('http://' + ip + '/api/aaaLogin.json?gui-token-request=yes', payload)
       	.then(response => {
       		var token = response.data.imdata[0].aaaLogin.attributes.token;
       		var urlToken = response.data.imdata[0].aaaLogin.attributes.urlToken;
       		removesessionStorage()
       		setCredentials(ip, username, pwd, token, urlToken);
        }, 	response => {
           // error callback
       	});
}

export function setCredentials(ip, username, pwd, token, urlToken){
    sessionStorage.setItem("ip", ip);
    sessionStorage.setItem("name", username);
    sessionStorage.setItem("pwd", pwd);
    sessionStorage.setItem("devcookie", token);
    sessionStorage.setItem("challenge", urlToken);
    sessionStorage.setItem("tokenEndTime", tokenEndTime())
}

export function removesessionStorage(){
	var all = ['ip', 'name', 'pwd', 'token', 'token_endtime', 'header']
	for (var i = 0; i < all.length; i++){
		sessionStorage.removeItem(all[i]);
	}
}

export function logout(){
	removesessionStorage()
}

