<template>
<div>
    <el-container class="container">
        <el-main class="login">
            <p> <strong>Hi, welcome to ACI-API. </strong> </p><p>This 'application' shows how <strong> easy </strong> it can be to <strong> interact with the Cisco ACI REST API </strong><br> to speed up application network deployment for non-ACI experts. </p>
            <h5 v-on:click="readmore = !readmore" v-if='readmore == false'> Check the reachability requirements. </h5><h5 v-else v-on:click="readmore = !readmore"> Hide reachability requirements. </h5>
            <div v-if='readmore'>
            <p > To ensure proper connectivity to the APIC, ensure IP reachability between the APIC and '{{locationIP.hostname}}'. Also make sure the 'Allow Origins' is set correctly. You can do that in <strong> Fabric > Fabric Policies > Policies > Pod > Management Access > Default. </strong> Under HTTP set the <strong> 'Allow Origins' to '{{locationIP.ip}}'</strong>. This will have no impact accessing the GUI.</p>
            <p class="redalert">This application uses HTTP to GET/POST commands. Any action you take is strictly at your own risk. </p>
            </div>
        </el-main>
    </el-container>
    <el-container class="container">
        <el-main>
            <span class="card-title">APIC Credentials</span>
            <div class="credinput">
                <el-input placeholder="APIC IP address" v-model="login_creds.ip" clearable> </el-input>
                <el-input placeholder="Username" v-model="login_creds.username" clearable> </el-input>
                <el-input placeholder="Password" v-model="login_creds.pwd" show-password></el-input>
                <el-button type="primary" :disabled=isLoginDisabled v-on:click="login()" >Login</el-button>  
            </div>
            <p class="redalert"> {{errorMessage}} </p>
        </el-main>


    </el-container>   
</div>
</template>
