<script>

import { login } from '../utils/auth';
import isip from 'is-ip';
export default {
    data(){
        return {
            login_creds: {
                username:'',
                ip:'',
                pwd:''
            },
            blogtopics: [],
            currenttopic: 'all',
            tag: '',
            loading: false,
            locationIP: {},
            errorMessage: '',
            readmore: false,
            showpwd: false
        }
    },

    created () {
        this.getServerIP();
    },

    methods: {
        getServerIP(){
            var x = location.hostname
            var y = location
            this.locationIP['hostname'] = x
            this.locationIP['ip'] = y
        },

        login(){
            login(this.login_creds.ip, this.login_creds.username, this.login_creds.pwd);
            if (sessionStorage.getItem("devcookie") !== null){
                this.$router.push({ name: 'Start'})
            }
            else {
                this.errorMessage = "Please ensure you have set the 'Allow Origin' correctly and provided the right credentials."
            }
        },
    },

    computed: {
        isLoginDisabled(){
            return (this.login_creds.pwd.length <= 0 || this.login_creds.username.length <= 0 || isip(this.login_creds.ip) == false)
        },
   
    }    
}

</script>

