<template>
<el-container class="container">
    <el-main>
        <div v-if="succes == false" class="flowEpg" >
            <span class="card-title">Contract Flow</span>
            <el-steps :active="active" simple>
                <el-step title="Filters " description="Create Filters & Entries"></el-step>
                <el-step title="Contracts " description="Create Contracts & Subjects"></el-step>
                <el-step title="Add Contract" description="Assign Contract to EPG"></el-step>

            </el-steps>
            <div v-if="active === 0">
                <div>

                    <p v-if="filterAndEntryCheck === true"><span  class="green"> Filter and/or Entry has been created </span></p>
                    <p> 1. Select a Tenant </p>
                        <el-select filterable clearable v-model="flowContract.tn" placeholder="Select a Tenant">
                            <el-option v-for="tn in tenant_name_list" :value="tn"></el-option>
                        </el-select>
                </div>
                <div v-if="flowContract.tn">
                    <p> 2. 
                    <span v-if="filters.length > 0">
                        <span v-if="createNewFilter" class="link" v-on:click="createNewFilter = !createNewFilter"> Select</span><span v-else> Select </span> or 
                    </span>
                    <span v-if="createNewFilter == false" class="link" v-on:click="createNewFilter = !createNewFilter">create</span><span v-else> create </span> a Filter <el-tooltip class="item" effect="dark" content="A filter allows you to filter traffic, based on one or more specific protocols and/or ports" placement="top-start"><i class="el-icon-question"></i></el-tooltip> </p>                    

                    <el-select filterable clearable v-if="createNewFilter == false && filters.length > 0" v-model="flowContract.flt" placeholder="Select a Filter">
                        <el-option v-for="flt in filters" :value="flt"></el-option>
                    </el-select>     
                                   
                    <el-input v-if="createNewFilter == true" class="el-input-large" v-model="flowContract.flt" placeholder="New Filter name" clearable></el-input>
                </div>
                <div v-if="flowContract.flt">
                    <p> 3. Provide the name of the entry <el-tooltip class="item" effect="dark" content="An entry specifies a specific protocol and/or port ranges "><i class="el-icon-question"></i></el-tooltip></p>
                    <el-input class="el-input-large" v-model="flowContract.entry" placeholder="New Entry name" clearable></el-input>
                </div>      
                <div v-if="flowContract.entry">
                    <p> 4. Provide some additional parameters</p>
                    <el-select filterable clearable v-model="flowContract.etherType" placeholder="Select an EtherType">
                        <el-option v-for="eT in etherType" :value="eT"></el-option>
                    </el-select>    
                    
                    <el-select v-if="flowContract.etherType == 'IP' || flowContract.etherType == 'IPv4' || flowContract.etherType == 'IPv6' || flowContract.etherType == 'ARP'" filterable clearable v-model="flowContract.ipprotocol" placeholder="Select an IP Protocol">
                        <el-option v-for="eTP in etherTypeIPProtocolF()" :value="eTP"></el-option>
                    </el-select>   

                    <el-input class="el-input-small" v-model="flowContract.sfport" placeholder="Source Port From" clearable></el-input>
                    <el-input class="el-input-small" v-model="flowContract.stport" placeholder="Source Port To" clearable></el-input>
                    <el-input class="el-input-small" v-model="flowContract.dfport" placeholder="Destination Port From" clearable></el-input>
                    <el-input class="el-input-small" v-model="flowContract.dtport" placeholder="Destination Port To" clearable></el-input>
                    <el-button type="primary" v-on:click="createFilterAndEntry()">Create Filter & Entry</el-button>                     
                </div>
            </div>  
            <div v-else-if="active === 1">
                <div v-if="flowContract.tn">
                    <p v-if="contractAndSubjectCheck === true"><span class="green"> Contract and/or Subject has been created </span></p>
                    <p> 1. 
                    <span v-if="contracts.length > 0">
                        <span v-if="createNewContract" class="link" v-on:click="createNewContract = !createNewContract"> Select</span><span v-else> Select </span> or 
                    </span>
                    <span v-if="createNewContract == false" class="link" v-on:click="createNewContract = !createNewContract">create</span><span v-else> create </span> a Contract <el-tooltip class="item" effect="dark" content="A contract allows you to filter traffic, based on one or more specific protocols and/or ports" placement="top-start"><i class="el-icon-question"></i></el-tooltip> </p>

                    <el-select filterable clearable v-if="createNewContract == false && contracts.length > 0" v-model="flowContract.contract" placeholder="Select a Filter">
                        <el-option v-for="ctr in contracts" :value="ctr"></el-option>
                    </el-select>     
                                   
                    <el-input v-if="createNewContract == true" class="el-input-large" v-model="flowContract.contract" placeholder="New Contract name" clearable></el-input>
                </div>
                <div v-if="flowContract.contract">
                    <p> 2. 
                    <span v-if="subjetsInContract.length > 0">
                        <span v-if="createNewSubject" class="link" v-on:click="createNewSubject = !createNewSubject"> Select</span><span v-else> Select </span> or 
                    </span>
                    <span v-if="createNewSubject == false" class="link" v-on:click="createNewSubject = !createNewSubject">create</span><span v-else> create </span> a Subject <el-tooltip class="item" effect="dark" content="A contract allows you to filter traffic, based on one or more specific protocols and/or ports" placement="top-start"><i class="el-icon-question"></i></el-tooltip> </p>

                    <el-select filterable clearable v-if="createNewSubject == false && subjetsInContract.length > 0" v-model="flowContract.subject" placeholder="Select a Subject">
                        <el-option v-for="ctr in subjetsInContract" :value="ctr"></el-option>
                    </el-select>     
                                   
                    <el-input v-if="createNewSubject == true" class="el-input-large" v-model="flowContract.subject" placeholder="New Subject name" clearable></el-input>
                </div>
                <div  v-if="flowContract.subject">
                    <p> 3. Select one or more filters from the 'Common' or '{{flowContract.tn}}' tenant.</p>
                    <el-select filterable multiple  v-model="flowContract.filters" placeholder="Select a Filter">
                        <el-option v-for="filter in filters" :value="filter"></el-option>
                    </el-select> 
                    <p><el-button :disabled=isFilterDisabled type="primary" v-on:click="createContractSubject()">Create Contract & Subject</el-button></p>                     
                </div>            
            </div>
            <div v-else-if="active === 2">
                <div class="flowEpg">
                    <p> 1. Add to EPG or L3-Out? </p>
                    <div>
                        <el-radio-group v-model="radio3">
                            <el-radio-button label="EPG"></el-radio-button>
                            <el-radio-button label="L3-Out"></el-radio-button>
                        </el-radio-group>
                    </div>
                    <div v-if="radio3 == 'EPG'">                   
                        <p> 2. Select an EPG in the '{{this.flowContract.tn}}' tenant </p>   
                        <el-select filterable clearable v-model="epg_selection" value-key="epg" placeholder="Select an EPG">
                            <el-option v-for="epg in allepg" :label="String(epg.epg + ' | AP: ' + epg.ap)" :key="epg.id" :value="epg"></el-option>
                        </el-select>
                    </div>
                    <div v-else>                   
                        <p> 2. Select a L3-Out </p>   
                        <el-select filterable clearable v-model="l3_selection" placeholder="Select a L3 out">
                            <el-option v-for="epg in l3out_name_list" :value="epg"></el-option>
                        </el-select>
                    </div>                    
                    <div v-if="epg_selection || l3_selection">
                        <p> 3. Select one or more contracts </p>
                        <el-select filterable multiple  v-model="flowContract.contract" placeholder="Select a Contract">
                            <el-option v-for="contract in contracts" :value="contract"></el-option>
                        </el-select>     
                        <p> 4. Do you want to consume and/or provide the contract(s)? </p>              
                        <el-checkbox v-model="consuming">Consuming</el-checkbox>
                        <el-checkbox v-model="providing">Providing</el-checkbox>
                        
                    <div v-if="radio3 == 'EPG'">
                        <p><el-button :disabled=isContractEpgDisabled type="primary" v-on:click="createContractToEpg()">Add Contract(s) to EPG</el-button></p>             
                    </div>
                    </div>            
                </div>            
            </div>
            <hr>
            <el-button :disabled="active > 0 ? false : true" type="primary" v-on:click="previous">Previous step</el-button>         
            <el-button :disabled=isNextDisabled type="primary" v-on:click="next">Next step</el-button>               
        </div>
    </el-main>
</el-container>
</template>


<script>

import { getTentants, getFiltersApi, getContractsApi, getSubjects, getAps, getEpgs, getApsMulti, getL3OutApi} from '../utils/getApic';
import { parseTentants, parseFilters, parseContracts, parseSubjects, parseApsMultiple, parseL3Out } from '../utils/parse';
import { postFilterEntry, postEntry, postContractAndSubject, postFilterToSubject, postContractToEpg } from '../utils/postApic';

import moment from 'moment'
export default {
    components: {
    },
    data(){
        return {
            filterAndEntryCheck: false,
            contractAndSubjectCheck: false,
            active: 0,
            radio3: 'EPG',
            loading: {
                tenant: true,
            },
            epg_selection: '',
            l3_selection: '',
            consuming: false, 
            providing: false,
            createNewFilter: false,
            createNewContract: false,
            createNewSubject: false,
            tenant_name_list: [],
            filters: [],
            contracts: [],
            l3out_name_list: [],
            subjetsInContract: [],
            allfilters: [],
            flowContract: {
                tn:'',
                entry: '',
                flt: '',
                etherType: 'IPv4',
                ipprotocol: 'tcp',
                filters: [],
                contract: [],
            },
            ap_list_multiple: [],
            allepg: [],
            succes: false,
            etherType: ['IP', 'IPv4', 'IPv6', 'MPLS Unicast', 'Unspecified', 'Trill', 'ARP', 'FCOE', 'MAC Security'],
            etherTypeIPProtocol: ['eigrp', 'egp', 'icmp', 'icmpv6', 'igmp', 'igp', 'l2tp', 'ospfigp', 'pim', 'tcp', 'udp', 'Unspecified'],
            etherTypeArpProtocol: ['ARP Reply', 'ARP request', 'Unspecified']
        }
    },

    created () {
        this.tenants();
    },    

    watch: {
        'flowContract.tn': function (val) {
            this.getFilters()

        },
        'flowContract.contract': function (val) {
            this.subjects()
            this.getFilters('common')
        },        
        'filters': function (val) {
            if (this.filters.length == 0) {
                this.createNewFilter = true
            }
            else {
                this.createNewFilter = false
            }
        },

        'radio3': function(){
            if (this.radio3 === 'L3-Out'){
                this.getL3Out('common')
                this.epg_selection = ''
            }
            else {
                this.l3_selection = ''
            }
        },

        'active': function (val) {
            if (this.active == 1){
                this.filterAndEntryCheck = false
                this.getContracts()
                this.getFilters('common')            
            }
            else if (this.active === 2){
                this.contractAndSubjectCheck = false
                this.getEgpDomain()
                this.getContracts('common')
            }
            else if (this.active === 0){
                this.contractAndSubjectCheck = false
                this.getFilters()
            }
        },

    },

    methods: {
        next() {
            if (this.active++ > 2) ;
        },
        
        previous() {
            if (this.active-- > 2);
        },        

        createContractToEpg(){
            for (var j = 0; j < this.flowContract.contract.length; j++){
                if (this.providing == true){
                    postContractToEpg(this.flowContract.tn, this.epg_selection.ap, this.epg_selection.epg, this.flowContract.contract[j], 'fvRsCons').then(response => {
                        },  response => {
                           // error callback
                    });
                }
                if (this.consuming == true){
                    postContractToEpg(this.flowContract.tn, this.epg_selection.ap, this.epg_selection.epg, this.flowContract.contract[j], 'fvRsProv').then(response => {
                        },  response => {
                           // error callback
                    });                    
                }
            }            
        },

        createContractSubject(){
            if (this.createNewSubject){
                postContractAndSubject(this.flowContract).then(response => {
                    },  response => {
                       
                });
            }   
            else {
                for (var j = 0; j < this.flowContract.filters.length; j++){
                    postFilterToSubject(this.flowContract.tn, this.flowContract.contract, this.flowContract.subject, this.flowContract.filters[j]).then(response => {
                        },  response => {
                           // error callback
                    });
                }
            }
            this.removeContractSubject()
            this.getContracts()             
        },

        removeContractSubject(){
            this.contractAndSubjectCheck = true;
            delete this.flowContract.subject
        
        },        

        removeFilterEntry(){
            this.filterAndEntryCheck = true;
            this.flowContract.etherType = 'IPv4'
            this.flowContract.ipprotocol = 'tcp'
            this.flowContract.filters = []
            this.flowContract.contract = []
            this.flowContract.entry = ''
            this.flowContract.flt = ''
            this.createNewFilter = false            
        },

        createFilterAndEntry(){
            if (this.createNewFilter){
                postFilterEntry(this.flowContract).then(response => {
                    },  response => {
                       // error callback
                });
            }
            else {
                postEntry(this.flowContract).then(response => {
                    },  response => {
                       // error callback
                });
            } 
            this.removeFilterEntry()
            this.getFilters()                           
        },

        getEgpDomain(){
            var aplist = []
            var ap_spec = {}
            var total = {}
            var totalegp = []

            getAps(this.flowContract.tn).then(response => {
                for (var s = 0; s < response.data.imdata.length; s++){
                    var ap_spec = {}
                    ap_spec.name = response.data.imdata[s].fvAp.attributes.name
                    ap_spec.tenant = this.flowContract.tn
                    aplist.push(ap_spec)
                }
            })
            setTimeout( () => {
                for (let d = 0; d < aplist.length; d++){
                    getEpgs(aplist[d].tenant, aplist[d].name).then(response => {
                        for (var p = 0; p < response.data.imdata.length; p++){
                            var total = {}
                            total.ap = aplist[d].name
                            total.epg = response.data.imdata[p].fvAEPg.attributes.name
                            totalegp.push(total)
                        }                 
                        },  response => {
                       // error callback
                    });
                }
                this.allepg = totalegp
            }, 1000);  
        },

        tenants(){
            this.tenant_name_list = []
            getTentants().then(response => {
                this.tenant_name_list = parseTentants(response.data)
            })
        }, 

        getL3Out(){
            this.l3out_name_list = []
            var args = [...arguments]
            args.push(this.flowContract.tn)
            getL3OutApi(args).then(response => {
                this.l3out_name_list = parseL3Out(response.data)
            })
        },

        getContracts(){
            this.contracts = []
            var args = [...arguments]
            args.push(this.flowContract.tn)
            getContractsApi(args).then(response => {
                console.log(response.data)
                this.contracts = parseContracts(response.data)
            })
        },      

        subjects(){
            this.subjetsInContract = []
            getSubjects(this.flowContract.tn, this.flowContract.contract).then(response => {
                this.subjetsInContract = parseSubjects(response.data)
            })
        }, 

        getFilters(){
            this.filters = []
            var args = [...arguments]
            args.push(this.flowContract.tn)            
            getFiltersApi(args).then(response => {
                this.filters = parseFilters(response.data)
            })
        }, 

        etherTypeIPProtocolF(){
            if (this.flowContract.etherType == 'IP' || this.flowContract.etherType == 'IPv4' || this.flowContract.etherType == 'IP'){
                // this.flowContract.ipprotocol = 'tcp'
                return this.etherTypeIPProtocol
            }
            else if (this.flowContract.etherType == 'ARP'){
                this.flowContract.ipprotocol = 'ARP Reply'
                return this.etherTypeArpProtocol
            }
            else {
                this.flowContract.ipprotocol = ''
            }
        },    
    },

    computed: {
        isNextDisabled(){
            return (this.active >= 2 || this.flowContract.tn == '' )
        },
        isFilterDisabled(){
            return (this.flowContract.filters.length <= 0)
        },
        isContractEpgDisabled(){
            return (this.flowContract.contract.length <= 0 || (this.consuming == false && this.providing == false))
        },     
    }
}

</script>

