import Vue from 'vue';

//// Parse Get Tenant API Call and Sort the data alphabetically /////
export function parseTentants(data){
	var tenant_name_list = []
    for (var i = 0; i < data.imdata.length; i++){
        tenant_name_list.push(data.imdata[i].fvTenant.attributes.name)
    }
    tenant_name_list.sort(new Intl.Collator('en').compare)
    return tenant_name_list
}

