export function parseL3Out(data){
    var l3out_name_list = []
    for (var j = 0; j < data.imdata.length; j++){
        if (data.imdata[j].fvTenant.children){
            for (var k = 0; k < data.imdata[j].fvTenant.children.length; k++){
                l3out_name_list.push(data.imdata[j].fvTenant.children[k].l3extOut.attributes.name)
            }
        }
    }    
    return l3out_name_list
}

