export function parseBds(data){
	var bd_name_list = []
	if (data.imdata.length !== undefined){
		try {
		    for (var j = 0; j < data.imdata.length; j++){
		        var bd_info = {}
		        bd_info.name = data.imdata[j].fvBD.attributes.name
		        if (data.imdata[j].fvBD.children){
			        bd_info.ip = data.imdata[j].fvBD.children[0].fvSubnet.attributes.ip
			        bd_info.scope = data.imdata[j].fvBD.children[0].fvSubnet.attributes.scope
		    	}
		        bd_name_list.push(bd_info)
		    }
		    return bd_name_list
		}
		catch(error){
			console.error(error);
		}
	}
}

export function parseVmmDom(data){
	var vmmDom = []
	for (var j = 0; j < data.imdata.length; j++){
		var domain_info = {}
        domain_info.type = 'Virtual'
        domain_info.name = data.imdata[j].vmmDomP.attributes.name
        domain_info.dn =  data.imdata[j].vmmDomP.attributes.dn
        vmmDom.push(domain_info)
    }
    return vmmDom
}

export function parsePhysDom(data){
	var physDom = []
	for (var j = 0; j < data.imdata.length; j++){
		var domain_info = {}
        domain_info.type = 'Physical'
        domain_info.name = data.imdata[j].physDomP.attributes.name
        domain_info.dn = data.imdata[j].physDomP.attributes.dn
        physDom.push(domain_info)
    }
    return physDom
}

export function parseEpgDomainCheck(data){
	var epgdomain = []
    for (var j = 0; j < data.imdata.length; j++){
        var vmm_specs = {}
        vmm_specs.domain = data.imdata[j].fvRsDomAtt.attributes.tDn
        vmm_specs.domain = vmm_specs.domain.split('dom-').pop();
        vmm_specs.domain = vmm_specs.domain.split('phys-').pop();
        vmm_specs.pushpolicy = data.imdata[j].fvRsDomAtt.attributes.resImedcy
        epgdomain.push(vmm_specs)
    }

    return epgdomain
}

