export function postBridgeDomain(tenant, vrf, bd, bddg, bdtype){
    var payload = {
        "fvBD":
            {"attributes":
                {"dn":"uni/tn-" + tenant + "/BD-" + bd + "","mac":"00:22:BD:F8:19:FF","name":"" + bd + "","rn":"BD-" + bd + "","status":"created"
            },"children":
                [{"fvSubnet":
                    {"attributes":
                        {"dn":"uni/tn-" + tenant + "/BD-" + bd + "/subnet-[" + bddg + "]","ctrl":"","ip": bddg,"scope": bdtype, "rn":"subnet-[" + bddg + "]","status":"created"}
                        ,"children":[]}},
                            {"fvRsCtx":
                                {"attributes":
                                    {"tnFvCtxName":"" + vrf + "","status":"created,modified"},"children":[]}
                                }]
                            }
                        }
    return postCall('/api/node/mo/uni/tn-' + tenant + '/BD-' + bd + '.json', payload)
}

export function postEndPointGroup(tenant, ap, epg, bd){
    var payload = {"fvAEPg":{"attributes":{"dn":"uni/tn-" + tenant + "/ap-" + ap + "/epg-" + epg + "","name":"" + epg + "","rn":"epg-" + epg + "","status":"created"},"children":[{"fvRsBd":{"attributes":{"tnFvBDName": bd,"status":"created,modified"},"children":[]}}]}}

    return postCall('/api/node/mo/uni/tn-' + tenant + '/ap-' + ap + '/epg-' + epg + '.json', payload)
}

export function FilterAndEntry(tenant, filter, entry, ethertype, protocol, sourcestart, sourceend, deststart, destend){
    var payload = {"vzFilter":{"attributes":{"dn":"uni/tn-" + tenant + "/flt-" + filter,"name":filter,"rn":"flt-" + filter,"status":"created,modified"},"children":[{"vzEntry":{"attributes":{"dn":"uni/tn-" + tenant + "/flt-" + filter + "/e" + entry,"name":entry,"etherT":ethertype,"prot":protocol,"sFromPort":sourcestart,"sToPort":sourceend,"dFromPort":deststart,"dToPort":destend,"rn":"e" + entry,"status":"created,modified"},"children":[]}}]}}

    return postCall('/api/node/mo/uni/tn-' + tenant + '/flt-' + filter + '.json', payload)
}

export function postEpgToDomain(epg, domain, vmmpolicy){
    var payload = {"fvRsDomAtt":{"attributes":{"resImedcy":vmmpolicy,"tDn":domain.dn,"status":"created"},"children":[{"vmmSecP":{"attributes":{"status":"created"},"children":[]}}]}}
    
    return postCall('/api/node/mo/uni/tn-' + epg.tn + '/ap-' + epg.ap + '/epg-' + epg.epg + '.json', payload)
}

export function postFilterEntry(flowContract){
    var payload = {"vzFilter":{"attributes":{"dn":"uni/tn-" + flowContract.tn + "/flt-" + flowContract.flt,"name": flowContract.flt, "rn":"flt-" + flowContract.flt,"status":"created,modified"},"children":[{"vzEntry":{"attributes":{"dn":"uni/tn-" + flowContract.tn + "/flt-" + flowContract.flt + "/e-" + flowContract.entry,"name":flowContract.entry,"etherT":flowContract.etherType.toLowerCase(),"prot":flowContract.ipprotocol.toLowerCase(),"sFromPort":flowContract.sfport,"sToPort":flowContract.stport,"dFromPort":flowContract.dfport,"dToPort":flowContract.dtport,"rn":"e-" + flowContract.entry,"status":"created,modified"},"children":[]}}]}}
    
    return postCall('/api/node/mo/uni/tn-' + flowContract.tn + '/flt-' + flowContract.flt + '.json', payload)
}

export function postEntry(flowContract){
    var payload = {"vzEntry":{"attributes":{"dn":"uni/tn-" + flowContract.tn + "/flt-" + flowContract.flt + '/e-' +flowContract.entry, "name":flowContract.entry,"etherT":flowContract.etherType.toLowerCase(),"status":"created,modified","prot":flowContract.ipprotocol.toLowerCase(),"sFromPort":flowContract.sfport,"sToPort":flowContract.stport,"dFromPort":flowContract.dfport,"dToPort":flowContract.dtport,"rn":"e-" + flowContract.entry,"status":"created,modified"},"children":[]}}
    
    return postCall('/api/node/mo/uni/tn-' + flowContract.tn + '/flt-' + flowContract.flt + '/e-' + flowContract.entry + '.json', payload)
}

export function postContractAndSubject(flowContract){
    var flt_list = []
    for (var j = 0; j < flowContract.filters.length; j++){
            flt_list.push({"vzRsSubjFiltAtt":{"attributes":{"status":"created,modified","tnVzFilterName":flowContract.filters[j],"directives":"none"},"children":[]}},)
        }

    var payload = {"vzBrCP":{"attributes":{"dn":"uni/tn-" + flowContract.tn + "/brc-" + flowContract.contract,"name":flowContract.contract,"rn":"brc-"+ flowContract.contract,"status":"created"},"children":[{"vzSubj":{"attributes":{"dn":"uni/tn-" + flowContract.tn + "/brc-" + flowContract.contract + "/subj-" + flowContract.subject,"name":flowContract.subject,"rn":"subj-" + flowContract.subject,"status":"created"},"children":flt_list}}]}}
    
    return postCall('/api/node/mo/uni/tn-' + flowContract.tn + '/brc-' + flowContract.contract + '.json', payload)
}

export function postFilterToSubject(tenant, contract, subject, filter ){
    var payload = {"vzRsSubjFiltAtt":{"attributes":{"tnVzFilterName":filter,"status":"created,modified"},"children":[]}}

    return postCall('/api/node/mo/uni/tn-' + tenant + '/brc-' + contract + '/subj-' + subject + '.json', payload)
}

export function postContractToEpg(tenant, ap, epg, contract, procon){
    // consuming: fvRsCons | providing: fvRsProv
    var payload
    if (procon == 'fvRsCons'){
        payload = {fvRsCons:{"attributes":{"tnVzBrCPName":contract,"status":"created,modified"},"children":[]}}
    }
    else {
        payload = {fvRsProv:{"attributes":{"tnVzBrCPName":contract,"status":"created,modified"},"children":[]}}

    }
    return postCall('/api/node/mo/uni/tn-' + tenant + '/ap-' + ap + '/epg-' + epg + '.json', payload)
}


