<template>
<div>
    <div class="container flow">
        <div class="flowEpg">
            <span class="card-title">Get Subnets</span>
            <p> Check the <strong> Bridge Domains and associated Default Gateway </strong> in all Tenants. </p>
            <hr>
            <div v-if="loading" class="progress">
                <div class="indeterminate"></div>
            </div>

            <div v-else class="subnets">
                <div  v-for="subs in subnet_list">
                    <div  v-for="(ind, it) in subs">
                        <span class="card-titles"> {{it}} </span>
                        <ul v-for="i in ind">
                           <li> {{ i.name }} - {{ i.ip }} - {{ i.scope }} </li>
                        </ul>

                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>


<script>

import { getTentants, getBds } from '../utils/getApic';
import { parseTentants, parseAps, parseVrfs, parseBds} from '../utils/parse';


import moment from 'moment'
export default {
    components: {
    },
    data(){
        return {
            subnet_list: [],
            tenant_name_list: [],
            loading: true,
        }
    },

    created () {
        this.tenants();
    },

    watch: {
        'tenant_name_list': function (val) {
            this.getsubs()
        },
    },

    methods: {
        tenants(){
            getTentants().then(response => {
                this.tenant_name_list = parseTentants(response.data)
            })
        },

        getsubs(){
            
            
            var megatotal = []
            for (let q = 0; q < this.tenant_name_list.length; q++){
                
                let tn = this.tenant_name_list[q]
                getBds(tn).then(response => {
                    var ja = {}
                    var bd = {}
                    var bd_name_list = parseBds(response.data)
                    for (let z = 0; z < bd_name_list.length; z++){
                        bd[bd_name_list[z].name] = bd_name_list[z]
                    }
                    ja[tn] = bd
                    this.subnet_list.push(ja) 
                });

                // 
                //                   
            }
            this.loading = false
        },
    },
}

</script>

<style scoped>

.flow {
    margin-bottom: 70px;
}

.card-titles {
    font-size: 16px;
    font-weight: 400;
    color: #005073;
}

.subnets ul, .subnets p {
    font-weight: 200;
    color: #005073;
}

</style>