<template>
<el-container class="container">
    <el-main>
        <div v-if="succes == false" >
            <span class="card-title">EPG Flow</span>
            <div v-if="loading.tenant" v-loading="loading">
            </div>
            <div class="flowEpg" v-else>
<!--                 <el-tag v-for="(item, index) in flowEpg" :label="String(index + item)" :key="epg"  type="info"></el-tag> -->
                <p> 1. <span v-if="createNewTn" class="link" v-on:click="createNewTn = !createNewTn"> Select </span><span v-else> Select </span> a Tenant or <span v-if="createNewTn == false" class="link" v-on:click="createNewTn = !createNewTn">create</span><span v-else> create </span> a new one. </p>

                <el-select filterable clearable v-if="createNewTn == false" v-model="flowEpg.tn" placeholder="Select a Tenant">
                    <el-option v-for="tn in tenant_name_list" :value="tn"></el-option>
                </el-select>

                <div v-if="createNewTn">
                    <el-input v-model="newTn" placeholder="New Tenant Name" clearable></el-input>
                    <el-button v-bind:disabled="newTn.length === 0 ? true : false" type="primary" v-on:click.prevent="postTn(newTn)">Create Tenant</el-button>
                </div>            
                <div v-if="flowEpg.tn">
                    <p> 2. <span v-if="createNewAp" class="link" v-on:click="createNewAp = !createNewAp"> Select </span><span v-else> Select </span> an Application Profile or <span v-if="createNewAp == false" class="link" v-on:click="createNewAp = !createNewAp">create</span><span v-else> create </span> a new one. </p>    

                    <el-select filterable clearable v-if="createNewAp == false" v-model="flowEpg.ap" placeholder="Select an Application Profile">
                        <el-option v-for="ap in ap_name_list" :value="ap"></el-option>
                    </el-select>

                    <div v-if="createNewAp">
                        <el-input v-model="newAp" placeholder="New Application Profile Name" clearable></el-input>
                        <el-button v-bind:disabled="newAp.length === 0 ? true : false" type="primary" v-on:click.prevent="postAp(newAp)">Create AP</el-button>
                    </div>          
                </div>
                <div v-if="flowEpg.ap">
                    <p> 3. <span v-if="createNewVrf" class="link" v-on:click="createNewVrf = !createNewVrf"> Select </span><span v-else> Select </span> a VRF or <span v-if="createNewVrf == false" class="link" v-on:click="createNewVrf = !createNewVrf">create</span><span v-else> create </span> a new one. </p>
                    
                    <el-select filterable clearable v-if="createNewVrf == false"v-model="flowEpg.vrf" placeholder="Select a VRF">
                        <el-option v-for="vrf in vrf_name_list" :value="vrf"></el-option>
                    </el-select>                    

                    <div v-if="createNewVrf">
                        <p> First select the 'Common' or '{{ flowEpg.tn }}' tenant, then give the new VRF a name. </p>
                        <el-select filterable clearable v-model="newVrf.tenant" placeholder="Select a Tenant">
                            <el-option value="Common"></el-option>
                            <el-option :value="flowEpg.tn"></el-option>
                        </el-select>                           
                        <el-input v-model="newVrf.name" placeholder="New VRF name" clearable></el-input>
                        <el-button :disabled=isVrfDisabled type="primary" v-on:click.prevent="postVrf(newVrf.tenant, newVrf.name)"> Create VRF</el-button>
                    </div>   
                </div>  

                <div v-if="flowEpg.vrf">
                    <p> 4. <span v-if="createNewBd" class="link" v-on:click="createNewBd = !createNewBd"> Select </span><span v-else> Select </span> a Bridge Domain or <span v-if="createNewBd == false" class="link" v-on:click="createNewBd = !createNewBd">create</span><span v-else> create </span> a new one </p>
                    
                    <el-select filterable clearable v-if="createNewBd == false" v-model="flowEpg.bd" placeholder="Select a Bridge Domain">
                        <el-option v-for="bd in bd_name_list" :value="bd.name">{{ bd.name }} - {{ bd.ip }}</el-option>
                    </el-select>                    

                    <div v-if="createNewBd">
                        <div>
                        <el-input v-model="newBd.name" placeholder="Bridge Domain Name" clearable></el-input>
                        <el-input v-model="newBd.ip" placeholder="Bridge Domain Gateway (CIRD notation - eg: 10.0.0.254/24)" clearable></el-input>
                        <el-radio v-model="newBd.type" label="private">Private to VRF</el-radio>
                        <el-radio v-model="newBd.type" label="public">Advertised Externally</el-radio>
                        </div>  
                        <el-button :disabled=isBdDisabled type="primary" v-on:click.prevent="postBd(newBd.name, newBd.ip, newBd.type)"> Create BD</el-button>
                    </div>   
                </div>  
                <div v-if="flowEpg.bd">
                    <p> 5. Create a new EPG </p> 
                    <el-input v-model="flowEpg.epg" placeholder="New EPG name" clearable></el-input>
                    <el-button :disabled="flowEpg.epg ? false : true" type="primary" v-on:click.prevent="postEpg(flowEpg.epg)"> Create EPG</el-button>                        
                </div>   
            </div>  
        </div>
        <div v-else>
            <p> Great, the flow has succesfully been submitted. </p>
            <div>
                <el-tag v-for="(item, index) in flowEpg" type="info"> {{index}}: {{item}} </el-tag>
            </div>
            <el-button type="primary" v-on:click.prevent="addToDomain()"> Add to a Domain </el-button>
            <el-button type="primary" v-on:click.prevent="createNewFlow()"> Create new flow </el-button>
        </div>                  
             
    </el-main>
</el-container>
</template>

<script>

import { getTentants, getAps, getVrfs, getBds } from '../utils/getApic';
import { parseTentants, parseAps, parseVrfs, parseBds} from '../utils/parse';
import { postTenant, postApplicationProfile, postVirtualRoutingAndForwarding, postBridgeDomain, postEndPointGroup } from '../utils/postApic';

import moment from 'moment'
import axios from 'axios'
import iscidr from 'is-cidr';
export default {
    components: {
        iscidr
    },
    data(){
        return {
            flowEpg: {},
            tenant_name_list: [],
            ap_name_list: [],
            vrf_name_list: [],  
            bd_name_list: [],            
            newTn: '',
            newAp: '',
            newVrf: {
                name: '',
                tenant: ''
            },
            newBd: {
                name: '',
                ip: '',
                type: 'private',
            },
            newEpg: '',
            loading: {
                tenant: true,
                ap: true,
                vrf: true,
                bd: true,
            },
            createNewTn: false,
            createNewAp: false,
            createNewVrf: false,
            createNewBd: false,
            succes: false,
        }
    },
    created () {
        this.tenants();
    },

    watch: {
        'flowEpg.tn': function (val) {
            if (this.flowEpg.tn == ''){
                delete this.flowEpg['tn'];
            }
            this.ap_name_list = []
            this.aps()
            delete this.flowEpg['ap'];
            delete this.flowEpg['vrf'];
            delete this.flowEpg['bd'];
        },
       'flowEpg.ap': function (val) {
            if (this.flowEpg.ap == ''){
                delete this.flowEpg['ap'];
            }        
            this.vrf_name_list = []
            this.vrfs()
        },

       'flowEpg.vrf': function (val) {
            if (this.flowEpg.vrf == ''){
                delete this.flowEpg['vrf'];
            }        
            this.bds()
        },

       'flowEpg.bd': function (val) {
            if (this.flowEpg.bd == ''){
                delete this.flowEpg['bd'];
            }        
        },

       'flowEpg.epg': function (val) {
            if (this.flowEpg.epg == ''){
                delete this.flowEpg['epg'];
            }        
        },        
    },

    methods: {

        /////////////// GET COMMANDS ///////////////

        tenants(){
            this.tenant_name_list = []
            getTentants().then(response => {
                this.tenant_name_list = parseTentants(response.data)
                this.loading.tenant = false
            })
        },

        aps(){
            this.ap_name_list = []
            getAps(this.flowEpg.tn).then(response => {
                this.ap_name_list = parseAps(response.data)
                this.loading.ap = false
            })
        },

        vrfs(){
            this.vrf_name_list = []
            var tns = ['common', this.flowEpg.tn]
            for (var i = 0; i < tns.length ; i++){            
                getVrfs(tns[i]).then(response => {
                    this.vrf_name_list = this.vrf_name_list.concat(parseVrfs(response.data))
                })
            }
            this.loading.vrf = false
        },
 
        bds(){
            this.bd_name_list = []
            getBds(this.flowEpg.tn).then(response => {
                this.bd_name_list = parseBds(response.data)
                this.loading.bd = false
            })            
        },                  

        /////////////// POST COMMANDS ///////////////

        postTn(tenant){
            postTenant(tenant).then(response => {
                    this.tenants()
                    this.createNewTn = !this.createNewTn
                },  response => {
                   // error callback
            });
        },            

        postAp(newAp){
            postApplicationProfile(this.flowEpg.tn, newAp).then(response => {
                    this.aps()
                    this.createNewAp = !this.createNewAp
                },  response => {
                   // error callback
            });
        },     

        postVrf(tenant, vrfname){
            postVirtualRoutingAndForwarding(tenant, vrfname).then(response => {
                    this.vrfs()
                    this.createNewVrf = !this.createNewVrf
                },  response => {
                   // error callback
            });
        },    

        postBd(bd, bddg, bdtype){
            postBridgeDomain(this.flowEpg.tn, this.flowEpg.vrf, bd, bddg, bdtype).then(response => {
                    this.bds()
                    this.createNewBd = !this.createNewBd
                },  response => {
                   // error callback
            });
        },    
 

        postEpg(epg){
            postEndPointGroup(this.flowEpg.tn, this.flowEpg.ap, epg, this.flowEpg.bd).then(response => {
                    this.succes = true
                },  response => {
                   // error callback
            });
        },                

        createNewFlow(){
            this.flowEpg = {}
            this.succes = false
        },

        addToDomain(){
            this.$router.push({ name: 'FlowVmm', params: this.flowEpg })
        }
    },

    computed: {
        isVrfDisabled(){
            return (this.newVrf.tenant.length <= 0 || this.newVrf.name.length <= 0 )
        },
        isBdDisabled(){
            return (this.newBd.name.length <= 0 || this.newBd.ip.length <= 0 || iscidr(this.newBd.ip) !== 4)
        },        
    }
}

</script>

<style scoped>

.el-button--primary {
    background-color: #005073;
    border-color: #fff;
    font-weight: 300;
}

.el-button--primary:hover {
    background-color: #e19915;
    border-color: #fff;
    font-weight: 300;
}

.el-button--primary {
    background-color: #005073;
    border-color: #fff;
    font-weight: 300;
}

.el-button--primary:hover {
    background-color: #e19915;
    border-color: #fff;
    font-weight: 300;
}

.el-button--primary.is-disabled {
    background-color: rgba(144,147,153,.1);
    border-color: rgba(144,147,153,.1);
    color: #909399;
}

