import axios from 'axios'
import { createHeader } from './code';


async function getCall(url){
    var auth = createHeader()
    return await axios.get('http://' + localStorage.getItem("ip") + url, { headers: auth, withCredentials: true})
    .then(response => {
        return response
    },  response => {
       // error callback
    })
}

export function getTentants(){
    return getCall('/api/class/fvTenant.json')
}

export function getAps(tenant){
    return getCall('/api/node/mo/uni/tn-' + tenant + '.json?query-target=children&target-subtree-class=fvAp')
}

export function getVrfs(tenant){
    return getCall('/api/node/mo/uni/tn-' + tenant + '.json?query-target=children&target-subtree-class=fvCtx')
}

