export function getL3OutApi(tenant){
    var tenant_filter = []
    for (var i = 0; i < tenant.length; i++){
        tenant_filter.push('eq(fvTenant.name,"' + tenant[i] + '")')
    } 
    return getCall('/api/node/class/fvTenant.json?query-target-filter=or(' + tenant_filter.toString() + ')&rsp-subtree=children&rsp-subtree-class=l3extOut')
}

